<template>
  <div v-if="slider != null" id="catBanner">
    <b-carousel
      id="carousel-cat-banner"
      v-model="slide"
      :interval="5000"
      indicators
      img-width="1920"
      img-height="300"
      style="text-shadow: 1px 1px 2px #333"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="hero-banner-carousel category"
    >
      <b-carousel-slide
        v-for="(bSlide, index) in slider.slides"
        :key="index"
        :style="{ backgroundImage: `url(${bSlide.optImage})` }"
        :id="'carousel-cat-banner' + index"
      >
        <div class="container">
          <span class="d-block text-uppercase text-light font-weight-normal">{{
            bSlide.title
          }}</span>
          <div
            v-if="bSlide.description != null"
            v-html="bSlide.description"
          ></div>

          <b-link
            v-if="bSlide.link != null"
            :to="bSlide.link"
            class="btn btn-blue ml-md-0"
            >{{ bSlide.buttonText }}</b-link
          >
        </div>
      </b-carousel-slide>
    </b-carousel>
  </div>
</template>

<script setup>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import { isMobile, isTablet, isBrowser } from "mobile-device-detect";

// Store references
const store = useStore();
const slider1 = computed(() => store.getters["category/getSlider"]);

// Data
const slide = ref(0);
const sliding = ref(false);

// Computed properties
const slider = computed(() => {
  let sliderValue = slider1.value;

  if (sliderValue != null) {
    if (sliderValue.slides != null) {
      sliderValue.slides.forEach((element) => {
        if (mobileCheck.value == "browser") {
          element.optImage = element.media.url;
        } else if (mobileCheck.value == "tablet") {
          element.optImage = element.media.larger;
        } else {
          element.optImage = element.media.medium;
        }
      });
    }
  }

  return sliderValue;
});

const mobileCheck = computed(() => {
  if (isBrowser) {
    return "browser";
  }
  if (isMobile && isTablet) {
    return "tablet";
  }
  return "mobile";
});

// Methods
const onSlideStart = () => {
  sliding.value = true;
};

const onSlideEnd = () => {
  sliding.value = false;
};
</script>
